import React, { useEffect, useState } from 'react';
import Balance from '../Balance/Balance';
import Card from '../Card/Card';

const Information = () => {
    const [person,setPerson] = useState([])
    const [writerinfo,setWriterInfo] = useState([])

    useEffect(()=>{
        fetch('./custom.JSON')
        .then(Response => Response.json())
        .then(data => setPerson(data))
    },[])

    const buttonHandaler = (single) => {
        const newWriter =[...writerinfo,single]
        setWriterInfo(newWriter)
    }

    return (
        <div className="container">
            <div className="row">
                <div className="col-md-10 row row-cols-3">
                {
                    person.map(single => <Card single={single}key={single.name}buttonHandaler={buttonHandaler}> </Card>)
                }
                </div>
                <div className="col-md-2">
                    <Balance writerinfo={writerinfo}> </Balance>

                </div>
            </div>
        </div>
    );
};

export default Information;


