import React from 'react';
import logo from '../img/logo.png'
import './Header.css'

const Header = () => {
    return (
        <>
        <div className="container">
            <div className="row">
                <div className="d-flex justify-content-between">
                <div><img src={logo} alt="" /></div>
                <nav className="navbar">
                    <a href="/home">Home</a>
                    <a href="/about">About</a>
                    <a href="/ContactUs">Contact Us</a>
                    <a href="/Poet">Poet</a>
                    <a href="/Detail">Detail</a>
                </nav>
                </div>
                    <div class="alert alert-success mt-4" role="alert">
                    <h4 class="alert-heading">Hello</h4> <hr />
                    <p><b>How do you cost to create a movie or drama including the fiction or novel?
                    The total Budget of making movie or drama is 100000 tk only.
                    There are every personal information of each poet or writer.</b></p>
                </div>
            </div>
        </div>
        </>
    );
};

export default Header;