import React from 'react';



const Balance = (props) => {
    const {writerinfo} = props
    let total =0
    let name = []
        for(const singlewriter of writerinfo){
            total = total + singlewriter.Price
            name =name + singlewriter.name 
        }
    


    return (
        <div className="content">
            <div className="second">
            <p className="comon"> <b> writer choose  : {props.writerinfo.length} </b></p>
            <p className="comon"> <b> Total Amount : {total}</b></p>
            <p className="comon"><b>{name}</b></p>
            </div>
        </div>
    );
};

export default Balance;