import React from 'react';
import './Card.css'

const Card = (props) => {
const {url,name,Country,Price,description,age} = props.single
    return (
        <div className=" ">

                <div className="card mt-3">
                <img src={url} className="card-img-top img-fulid" alt="" />
                <h3>{name}</h3>
                <div className="card-body">
                <h4>{Price} TK</h4>
                <span><b>{Country}</b></span> <span><b>age : {age}</b></span>
                <p className="card-text">{description.slice(0,120)}</p>
                <button className="btn btn-success"onClick={()=> props.buttonHandaler(props.single)}><i class="fas fa-shopping-cart"></i>Chose your writer</button>
                </div>
                </div>

        </div>

    );
};

export default Card;

